import { Controller, Inline, Text, Injection } from '@ali/4ever-cangjie';

export default function removeinjectedCardInfo(controller: Controller, node: Inline | Text): Controller {
  const { injections } = controller.value;
  const excludeInjections: Injection[] = injections.filter((i) => i.key !== node.key);
  controller.setInjections([...excludeInjections]);
  return controller;
}
